    this.company_id = getLoginInfo('company_object').id
    this.obtainProvinceAddressData()
    this.obtainAdminRoleDataList()
  },
  mounted () {
    this.$refs['accountForm'].clearValidate()
  },
  updated () {
    this.$refs['accountForm'].clearValidate()
  },
  methods: {
    addAccountClick (accountForm) {
      this.add()
    },
    add () {
      if (!this.accountInfo.user_account) {
        this.$message({
          type: 'error',
          message: `请输入用户账号`
        })
        return
      }
      if (!this.accountInfo.account_name) {
        this.$message({
          type: 'error',
          message: `请输入用户名`
        })
        return
      }
      if (!this.accountInfo.account_pwd) {
        this.$message({
          type: 'error',
          message: `请输入登陆密码`
        })
        return
      }
      if (!this.accountInfo.province_code) {
        this.$message({
          type: 'error',
          message: `请选择省`
        })
        return
      }
      if (!this.accountInfo.city_code) {
        this.$message({
          type: 'error',
          message: `请选择市`
        })
        return
      }
      if (!this.accountInfo.cd_code) {
        this.$message({
          type: 'error',
          message: `请选择区`
        })
        return
      }
      if (!this.accountInfo.admin_role) {
        this.$message({
          type: 'error',
          message: `请选择管理员角色`
        })
        return
      }
      let params = {
        company_id: this.company_id,
        user_name: this.accountInfo.user_account,
        password: this.accountInfo.account_pwd,
        real_name: this.accountInfo.account_name,
        province_code: this.accountInfo.province_code,
        city_code: this.accountInfo.city_code,
        role_id: this.accountInfo.admin_role
      }
      addAdminRole(params).then(res => {
        this.$message({
          type: 'success',
          message: `添加成功`
        })
        this.$router.go(-1)
      })
    },
    obtainAdminRoleDataList () {
      obtainAdminRoleData('').then(res => {
        this.roleList = res.data.items
      })
    },
    accountCancelClick () {
      this.$router.go(-1)
    },
    seeRoleClick () {
      this.roleHidden = true
    },
    roleCancelClick (event) {
      console.log(event)
      if (event.target.className === 'mask') {
        this.roleHidden = false
      }
    },
    obtainProvinceAddressData () {
      let _this = this
      obtainProvinceAddress('?pid=-1').then(res => {
        _this.provinceData = res.data
      })
    },
    obtainCityAddressData (pid) {
      obtainProvinceAddress('?pid=' + pid).then(res => {
        this.cityData = res.data
      })
    },
    obtainAreaAddressData (pid) {
      obtainProvinceAddress('?pid=' + pid).then(res => {
        this.areaData = res.data
      })
    },
    changeProvince (data) {
      let _this = this
      this.provinceData.forEach(function (item) {
        if (item.code === data) {
          _this.obtainCityAddressData(item.id)
        }
      })
    },
    changeCity (data) {
      let _this = this
      this.cityData.forEach(function (item) {
        if (item.code === data) {
          _this.obtainAreaAddressData(item.id)
        }
      })
    },
    dataStaticsSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0 || columnIndex === 1) {
        if (rowIndex >= 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        } else {
          return {
            rowspan: 3,
            colspan: 1
          }
        }
      }
    },
    auntManagerSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0 || columnIndex === 1) {
        if (rowIndex >= 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        } else {
          return {
            rowspan: 4,
            colspan: 1
          }
        }
      }
    },
    demandManagerSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0 || columnIndex === 1) {
        if (rowIndex >= 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        } else {
          return {
            rowspan: 3,
            colspan: 1
          }
        }
      }
    },
    courseManagerSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex >= 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        } else {
          return {
            rowspan: 5,
            colspan: 1
          }
        }
      } else if (columnIndex === 1) {
        if (rowIndex >= 1) {
          if (row.second !== this.courseManagerData[rowIndex - 1].second) {
            return {
              rowspan: 3,
              colspan: 1
            }
          } else {
            return {
              rowspan: 0,
              colspan: 0
            }
          }
        } else {
          return {
            rowspan: 3,
            colspan: 1
          }
        }
      }
    },
    orderManagerSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex >= 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        } else {
          return {
            rowspan: 3,
            colspan: 1
          }
        }
      } else if (columnIndex === 1) {
        if (rowIndex >= 1) {
          if (row.second !== this.orderManagerData[rowIndex - 1].second) {
            return {
              rowspan: 2,
              colspan: 1
            }
          } else {
            return {
              rowspan: 0,
              colspan: 0
            }
          }
        } else {
          return {
            rowspan: 2,
            colspan: 1
          }
        }
      }
    },
    creditManagerSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex >= 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        } else {
          return {
            rowspan: 4,
            colspan: 1
          }
        }
      } else if (columnIndex === 1) {
        if (rowIndex >= 1) {
          if (row.second !== this.creditManagerData[rowIndex - 1].second) {
            return {
              rowspan: 1,
              colspan: 1
            }
          } else {
            return {
              rowspan: 0,
              colspan: 0
            }
          }
        } else {
          return {
            rowspan: 2,
            colspan: 1
          }
        }
      }
    },
    complaintManagerSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex >= 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        } else {
          return {
            rowspan: 4,
            colspan: 1
          }
        }
      } else if (columnIndex === 1) {
        if (rowIndex >= 1) {
          if (row.second !== this.complaintManagerData[rowIndex - 1].second) {
            return {
              rowspan: 3,
              colspan: 1
            }
          } else {
            return {
              rowspan: 0,
              colspan: 0
            }
          }
        } else {
          return {
            rowspan: 3,
            colspan: 1
          }
        }
      }
    },
    systemManagerSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex >= 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        } else {
          return {
            rowspan: this.systemManagerData.length,
            colspan: 1
          }
        }
      } else if (columnIndex === 1) {
        if (rowIndex >= 1) {
          if (row.second !== this.systemManagerData[rowIndex - 1].second) {
            let array = this.systemManagerData.filter(value => value.second === row.second)
            return {
              rowspan: array.length,
              colspan: 1
            }
          } else {
            return {
              rowspan: 0,
              colspan: 0
            }
          }
        } else {
          let array = this.systemManagerData.filter(value => value.second === row.second)
          return {
            rowspan: array.length,
            colspan: 1
          }
        }
      }
    },
    accountManagerSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0 || columnIndex === 1) {
        if (rowIndex >= 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        } else {
          let array = this.accountManagerData.filter(value => value.second === row.second)
          return {
            rowspan: array.length,
            colspan: 1
          }
        }
      }
    }
  }
}
</script>
<style scoped>

.mask{
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 0;
  left: 0;
}
.scence{
  position: absolute;
  top: 45%;
  left: 50%;
  width: 70%;
  height:60%;
  padding-bottom: 12px;
  transform: translate(-50%, -50%);
  /* border-radius: 10px;
  background: #fff; */
}
td{
  padding: 35px 20px
}
</style>



